* {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	background-color: #eff3f7;
	height: 100%;
}

html,
body,
.questions {
	height: 100%;
	display: flex;
	justify-content: center;
}

form {
	display: flex;
	justify-content: flex-end;
	position: sticky;
}

form .button {
	margin: 10px;
	width: 20%;
	height: 20px;
}

.questions {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-wrap: wrap;
	padding: 50px;
	padding-top: 30px;
	width: 100%;
}

.questions .qAndA {
	width: 45%;
	background-color: white;
	margin-bottom: 20px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: space-between;
	font-weight: 450;
	padding: 10px 10px;
	border-radius: 5px;
	color: #2f2f31;
	font-size: 15px;
	box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
		0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.questions .qAndA .question {
	font-size: 20px;
	font-weight: 500;
	color: #364152;
	position: relative;
	max-width: 100%;
	top: -10px;
	padding: 0px;
	margin-right: 30px;
	margin-bottom: 0;
	box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
		0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

@media (max-width: 600px) {
	.questions {
		display: flex;
		padding: 50px;
		padding-top: 30px;
		width: 100%;
	}

	.questions .qAndA {
		width: 100%;
	}

	form {
		display: flex;
		justify-content: center;
	}

	form .button {
		margin: 10px;
		width: 30%;
		height: 20px;
	}
}
